
:root{
  --naranja: #f39327;
  --azul-fondo: #00253c;
  --azul-botones: #025a91;
  --shadow-componente: 0px 0px 1px 3px #025a9154;

  --radio-componente: 5px;

  /* boton cerrar componente */
  --height-btn: 30px;
  --width-btn: 55px;
  --radio-btn-x: 30px;
              /* boton cerrar :hover */
              --naranja-hover:#f39327;
              --transform-hover: scale(1.1);
  
  /* boton enviar */
  --width-btn: 100%;
  --height-btn: 50;
  --radio-btn-x: 50px;
              /* boton enviar :hover */
              --naranja-send-hover:#f39327;
              --transform-send-hover: scale(1.02);


  /* input*/
  --input-border: 2px solid #025a91 ;
              /* input :focus */
              --input-focus: 2px solid #f39327;

}


*{
  color:white;
  font-family: Inter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
  Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

.openNotification{
  width: 20%;
  height: 50px;
  background-color:#025a91;

  border-radius: 50px;
  border: none;
  padding: 0;
  font-size:1em;
  cursor: pointer;
  margin: 1em 0 0 1em;
  transition: 0.3s;
}

.openNotification:hover{
  background-color:#f39327;
  transform: scale(1.02)
}

.notification-wrapper {
  position: fixed;
  bottom: 10px;
  right: 10px;
  min-width: 150px;

}


/* Entra notificatión */




.notification-item {
  animation: SlideLeft .5s;
  animation-fill-mode: forwards;
  box-shadow: 0px 0px 1px 3px #025a9154;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 15px;
  background-color:  var(--azul-fondo);
}

.destacado{
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  background-color:#f39327;
  width: 20px;
  line-height: 20px;
  height: 20px;
  font-family: Inter;
  border-radius: 3px;
  margin: 0px;
  padding: 0px;

}
.notification-title{
  padding-left: 1em;
  padding-right: 1em;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

}



.info{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin: .3em;
  border-radius: 5px;
  margin-top: 0;
  background-color:var(--azul-botones)

}
.notification-item h2{
  display: inline-flex;
  margin:.5em;
  font-size: 1em;
  text-align: left;
}
.text-p{
  font-size: 1em;
  text-align: left;
}

.info-p{
  display: flex;
  text-align: right;
  font-size: 1em;
 color: var(--naranja);
 font-weight: bold;
 

}

/* Sale notificación */
.notification-item.exit {
  animation: SlideRight 1s;
  animation-fill-mode: forwards;
}

/* Efectos notificación */
@keyframes SlideLeft {
    0% {
      transform: translateX(120%);
  }

  100% {
    transform: translateX(0px);
  }
}

@keyframes SlideRight {
  0% {
    transform: translateX(0px);
}

100% {
  transform: translateX(120%);
}
}





.notification-item p {
  margin: 0;
  padding: 10px;
}

.notification-item .bar {
  height: 10px;
}