*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    color:white;
    
}

:root{
    --naranja: #f39327;
    --azul-fondo: #00253c;
    --azul-botones: #025a91;
    --shadow-componente: 0px 0px 1px 5px #025a9154;

    --radio-componente: 25px;

    /* boton cerrar componente */
    --height-btn: 30px;
    --width-btn: 55px;
    --radio-btn-x: 30px;
                /* boton cerrar :hover */
                --naranja-hover:#f39327;
                --transform-hover: scale(1.1);
    
    /* boton enviar */
    --width-btn: 100%;
    --height-btn: 50;
    --radio-btn-x: 50px;
                /* boton enviar :hover */
                --naranja-send-hover:#f39327;
                --transform-send-hover: scale(1.02);


    /* input*/
    --input-border: 2px solid #025a91 ;
                /* input :focus */
                --input-focus: 2px solid #f39327;

}


  
  .container{
      position:absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      align-items: center;
      width: 500px;
      max-height: 400px ;
      background-color:#00253c;
      flex-direction: column;
      justify-content: space-evenly;
      border-radius: 25px;
      box-shadow: 0px 0px 1px 5px #025a9154;
      
      
    }
    

.title{
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 1em 0;
    background: #00253c;
}

h2{
    background: #00253c;
}

.x{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 55px;
    height: 30px;
    border: none;
    background-color:#025a91;
    text-align: center;
    font-size:1em;
    font-weight: bold;
    border-radius: 25px;
    transition: 0.3s;
}

.x:hover{
    background-color: #f39327;
    transform: scale(1.1)
}

.description, h3{
    width: 90%;
    background: #00253c;
}

h3{
    margin-top: 1em;
    font-size: 1em;
    text-align: left;
    font-weight: 100;
}

form{
    background: #00253c;
  
}

input{
    margin-top: .5em;
    width: 100%;
    height: 50px;
    text-align: center;
    border:2px solid #025a91 ;
    background-clip: padding-box ;
    background: #00253c;
    border-radius: 10px;
    padding: 10px;
    font-size: 1.5em;
}

input:focus{
    border: 2px solid #f39327;
    outline:none;

}

::placeholder {
    font-size: .8em;
  }

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.deposit{

    width: 100%;
    height: 50px;
    background-color:#025a91;

    border-radius: 50px;
    border: none;
    padding: 0;
    font-size:1em;
    cursor: pointer;
    margin: 1em 0;
    transition: 0.3s;
}

.deposit:hover{
    background-color:#f39327;
    transform: scale(1.02)
}